import Vue from 'vue'
import BootstrapVue, { LayoutPlugin } from 'bootstrap-vue' // added
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/Login'
import Portal from '@/components/Portal'
import Settings from '@/components/Settings'
import Profile from '@/components/Profile'
import Account from '@/components/Account'
import Parameter from '@/components/Parameter'
import Information from '@/components/Information'
import Scraping from '@/components/Scraping'
import Map from '@/components/Map'
import Chart from '@/components/Chart'
import store from '@/store/store'

Vue.use(Router)
Vue.use(BootstrapVue)

import 'bootstrap/dist/css/bootstrap.css' // added
import 'bootstrap-vue/dist/bootstrap-vue.css' // added

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Root',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/settings',
      meta: { requiresAuth: true },
      name: 'Settings',
      component: Settings,
      children: [
        {
          path: '',
          name: 'Profile',
          component: Profile
        },
        {
          path: 'profile',
          name: 'Profile',
          component: Profile
        },
        {
          path: 'account',
          name: 'Account',
          component: Account
        },
        {
          path: 'parameter',
          name: 'Parameter',
          component: Parameter
        }
      ]
    },
    {
      path: '/portal',
      meta: { requiresAuth: true },
      component: Portal,
      children: [
        {
          path: '',
          name: 'Portal',
          component: Information
        },
        {
          path: 'information',
          name: 'Information',
          component: Information
        },
        {
          path: 'information',
          name: 'Information',
          component: Information
        },
        {
          path: 'map',
          name: 'Map',
          component: Map
        },
        {
          path: 'chart',
          name: 'Chart',
          component: Chart
        },
        {
          path: 'scraping',
          name: 'Scraping',
          component: Scraping
        }
      ]
    }
  ]
})

/**
 * Router
 */
router.beforeEach((to, from, next) => {

  const isLoggedIn = store.getters['authData/isLoggedIn']
  const token = localStorage.getItem('access')
  console.log('to.path=', to.path)
  console.log('isLoggedIn=', isLoggedIn)

  store.dispatch('messageData/clearMessages')

  // 
  if (to.matched.some(record => record.meta.requiresAuth)) {

    // 
    if (isLoggedIn) {
      console.log('User is already logged in. So, free to next.')
      next()

      // 
    } else {
      // 
      if (token != null) {
        console.log('User is not logged in. Trying to reload again.')

        store.dispatch('authData/reload')
          .then(() => {
            // 
            console.log('Succeeded to reload. So, free to next.')
            next()
          })
          .catch(() => {
            // 
            forceToLoginPage(to, from, next)
          })
      } else {
        // 
        forceToLoginPage(to, from, next)
      }
    }
  } else {
    // 
    console.log('Go to public page.')
    next()
  }
})

/**
 * 
 */
function forceToLoginPage (to, from, next) {
  console.log('Force user to login page.')
  next({
    path: '/login',
    // URL
    query: { next: to.fullPath }
  })
}

export default router



